<template>
<div id="QManage">
    <div id="inputMes">
    选择业务:<el-select v-model="business" placeholder="请选择业务" class="input">
        <el-option v-for="item in businesses" :label="item.businessName" :value="item.id"></el-option>
    </el-select>
    选择窗口:<el-select v-model="window" class="input" :disabled="hasChooseBusiness" v-loading.fullscreen.lock="hasgetwindows">
        <el-option v-for="item in windows" :label="item.windowNo" :value="item.id"></el-option>
    </el-select>
    预约状态:<el-select v-model="appointed" class="input">
        <el-option v-for="item in status" :label="item.label" :value="item.value"></el-option>
    </el-select>
    <el-button type="primary" size="medium" @click="query">查询</el-button>
    </div>
    <el-table :data="tabledata" style="width:56.1%;" stripe border v-loading="load">
        <el-table-column prop="queueNo" label="队列号" width="120"></el-table-column>
        <el-table-column prop="windowNo" label="窗口号" width="120"></el-table-column>
        <el-table-column prop="staffName" label="办理员工" width="120"></el-table-column> 
        <el-table-column prop="businessName" label="办理业务" width="120"></el-table-column>
        <el-table-column prop="handler" label="预约时间" width="120"></el-table-column>
        <el-table-column prop="transactName" label="办理状态" width="120"></el-table-column>
        
    </el-table>
</div>
</template>
<script>
export default {
    name:'queueTable',
    data(){
        return{
            tabledata:[],
            load:true,
            hasChooseBusiness:true,
            hasgetwindows:false,
            businesses:[],
            windows:[],
            status:[{label:'未办理',value:0},{label:'已办理',value:1},{label:'已过号',value:2}],
            business:'',
            window:'',
            appointed:''
        }
    },
    watch:{
        business:function(){
            this.window =''
            this.hasgetwindows = true
            this.axios.getWindows({'businessId':this.business}).then((res)=>{
                if(res.success){
                    this.windows = res.dataList
                    this.hasChooseBusiness = false
                    this.hasgetwindows = false
                }
            })
        }
    },
    mounted(){
        this.init()
    },
    methods:{
        init(){
            this.axios.getBusiness({}).then((res)=>{
                if(res.success){
                    this.businesses =res.dataList
                }
            })
            this.axios.getQueue({}).then((res)=>{
                if(res.success){
                    this.load = false
                    this.tabledata = res.dataList
                }
            })
        },
        is(row){
            if(row.transact == 0)
                return false
            return true
        },
        repeal(row){
            this.axios.dealWork({id:row.id,windowId:row.windowId,queueNum:row.queueNum}).then((res)=>{
                if(res.success){
                    this.$message.success("办理完毕")
                    this.init()
                }else
                    this.$message.error("办理失败")
            })
        },
        query(){
            this.load = true
            let data = {'businessId':this.business,'windowId':this.window,'transact':this.appointed}
            this.axios.getQueue(data).then((res)=>{
                if(res.success){
                    this.load = false
                    this.tabledata = res.dataList
                }else
                    this.$message.error('查询失败')
            })
        }
    }
}
</script>
<style lang="scss">
#QManage{
    #inputMes{
        position: relative;
        right: 135px;
        top: 90px;
        .input{
            width: 10%;
            margin-right: 20px;
        }
    }
    .el-table{
        border:1px solid #000;
        border-radius: 7px 7px 7px 7px;
        box-shadow:0px 0px 3px #000;
        position: relative;
        top:100px;
        left: 150px;
    }
}
</style>

